$button-group-intent-divider: hslax(var(--white-hsl),0.25) !default;

.#{$ns}-button-group{

  &:not(.#{$ns}-vertical) {
    .#{$ns}-button[class*=#{$ns}-intent-]{
      border: 0 solid $button-group-intent-divider;
      border-width: 0 1px;
      &:first-child,
      .#{$ns}-popover-wrapper:first-child & {
        border-left: 0;
      }
      &:last-child,
      .#{$ns}-popover-wrapper:last-child & {
          border-right: 0;
      }
    }
  }

  &.#{$ns}-vertical {
    .#{$ns}-button[class*=#{$ns}-intent-]{
      border: 0 solid $button-group-intent-divider;
      border-width: 1px 0;
      &:first-child,
      .#{$ns}-popover-wrapper:first-child & {
        border-top: 0;
      }
      &:last-child,
      .#{$ns}-popover-wrapper:last-child & {
        border-bottom: 0;
      }
    }
  }

}


