// Do not quote this value, for Less consumers.
// $ns: bp3;

// easily the most important variable, so it comes up top
// (so other variables can use it to define themselves)
// $pt-grid-size: 10px;

// https://ant.design/docs/spec/font#Font-Family
$pt-font-family:
  // Avenir,
  -apple-system,
  BlinkMacSystemFont,
  'Segoe UI',
  Roboto,
  'Helvetica Neue',
  Arial,
  'Noto Sans',
  sans-serif,
  'Apple Color Emoji',
  'Segoe UI Emoji',
  'Segoe UI Symbol',
  'Noto Color Emoji';

$pt-font-family-monospace:
  sfmono-regular,
  Consolas,
  liberation mono,
  Menlo,
  Courier,
  monospace;

$pt-border-radius: 2px;

$pt-font-size: $pt-grid-size * 1.4;
$pt-font-size-large: $pt-grid-size * 1.6;
$pt-font-size-small: $pt-grid-size * 1.2;

// $pt-line-height: ($pt-grid-size * 2.2) / $pt-font-size + 0.0001;

// flat input border
$pt-input-box-shadow: inset border-shadow(0.15);


